@import "../utils/theme";
.navbar{
    height: 60px;
    background-color: $primaty_color;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 3em;
    .brand{
        font-size: 20px;
        color: white;
        font-weight: bold;
    }
    .navRight{
        display: flex;
        align-items: center;
        .navLinks{
            margin: 0 20px;
            a{
                color: white;
                font-size: 14px;
                font-weight: 600px;
                text-decoration: none;
            }
        }
    }
}